/* -------- progress bar -------- */
:root {
    --length_of_bar: 0;
}
.bar {
    width: 70%;
    height: 2rem;
}
#main_bar {
    width: 20rem;
    height: 2rem;
    border-radius: 15px;
    background-color: rgb(0, 140, 255);
    position: absolute;
    z-index: -1;
    box-shadow: 1px 0 3px 0 rgb(18, 133, 227);
}
#progress_bar {
    width: 0;
    width: var(--length_of_bar);
    height: 2rem;
    border-radius: 15px;
    background-color: rgb(0, 221, 255);
    position: absolute;
    z-index: 1;
    box-shadow: 1px 0 3px 0 rgb(3, 173, 252);
}
#bar_text {
    font-size: 18px;
    font-weight: 300;
    text-align: center;
    width: 100%;
    height: 2rem;
    margin-top: 6px;
}

/* --------------Responsive design-------------- */

/* Mobile version (<600px) */
@media screen and (max-width: 599px) {
    .main {
        height: 18rem;
    }
    .glass {
        width: 40%;
        margin: 1rem auto;
        height: 10rem;
    }
    #counter {
        font-size: 1.5rem;
    }
}

/* Tablet version (portrait) >600px */
@media screen and (min-width: 600px) {
    .main {
        height: 22rem;
    }
    .glass {
        height: 12rem;
        margin: 2rem auto;
    }
    #counter {
        font-size: 1.5rem;
    }
}

/* Tablet version (landscape) >900px */
@media screen and (min-width: 900px) {
    .main {
        height: 24rem;
    }
    .glass {
        height: 14rem;
        margin: 2rem auto;
    }
    #counter {
        font-size: 2rem;
    }
}

/* Desktop version (>1200px) */
@media screen and (min-width: 1200px) {
    .main {
        height: 28rem;
    }
    .glass {
        height: 15rem;
        margin: 3rem auto;
    }
}
